
.floating-menu-wrapper{
  height: 60px;
  line-height: 60px;
  width: 60px;
  background: rgba(255, 99, 42, 0.8);
  position: fixed;
  right: 20px;
  bottom: 15px;
  border-radius: 50px;
  color: white;
  cursor: pointer;
  border: 1px solid black;
  z-index: 2;
}
.menu-circle{
  width: 100%;
  text-align: center;
}



